.Drawer__container {
  position: absolute;
  left: 0;
  top: 0;
  width: 450px;
  max-width: 100%;
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
  transition-property: -webkit-transform,transform,opacity;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
  -webkit-transition-timing-function: cubic-bezier(0.0,0.0,0.2,1);
       -o-transition-timing-function: cubic-bezier(0.0,0.0,0.2,1);
          transition-timing-function: cubic-bezier(0.0,0.0,0.2,1);
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Drawer__container.opened {
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}

.Drawer__container.full-height {
  height: 100%;
}

.Drawer__button {
  position: absolute;
  top: 0.5rem;
  right: -1.5rem;
  width: 1.5rem;
  height: 3.5rem;
  -webkit-box-shadow: 2px 1px 2px rgba(0, 0, 0, 0.3);
          box-shadow: 2px 1px 2px rgba(0, 0, 0, 0.3);
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 0;
  cursor: pointer;
  z-index: 1;
}

.Drawer__button:after {
  content: '\203A';
  font-size: 2rem;
  color: #555;
}

.Drawer__container.opened .Drawer__button:after {
  content: '\2039';
}

.Drawer__anchor {
  position: absolute;
  right: -40px;
  height: 100%;
  width: 40px;
}


